@use 'base/mixin' as m;
@use 'base/variables' as v;

.galeria {
    position: relative;
    top: 700px;
    border-radius: 10px;
    width: 95%;
    margin: 0 auto;
    background-color: v.$blanco;
    padding: 5rem;

    @include m.phone {
        top: 200px;
    }


    h2 {
        text-align: center;
        margin: 0;
        margin-bottom: 2rem
    }
}

.galeria-nft {
    @include m.reset-lyst;
    @include m.grid(2, 2rem);

    @include m.phone {
        @include m.grid(3, 2rem);
    }

    @include m.tablet {
        @include m.grid(4, 2rem);
    }

    @include m.desktop {
        @include m.grid(8, 2rem);
    }


}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;
    background-color: rgba(0, 0, 0, 0.5);

    picture {
        display: block;
        max-width: 80rem;

    }
}

.btn-close {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$negro;
    display: grid;
    place-items: center;
    font-weight: v.$w-700;

    &:hover {
        cursor: pointer;
    }
}