body {
  font-family: helvetica, arial, sans-serif;
  font-size: 1.6rem;
  background: rgb(26, 51, 208);
  background: linear-gradient(90deg, rgba(26, 51, 208, 0.6) 0%, rgba(218, 0, 55, 0.6) 100%);
  width: 100%;
  height: 100%;
  position: relative;
}
body.scroll-body {
  padding-top: 70px;
}
body.fixed-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: black;
  line-height: 3rem;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.6rem;
}

h3 {
  font-size: 2.2rem;
}

a {
  text-decoration: none;
  color: black;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

